@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@500;700;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-gap: 3rem;
  gap: 3rem;
  font-size: 2.6rem;
  font-weight: 700;
  text-decoration: none;
  list-style: none;
  padding-top: 3rem;
  padding-left: 5rem;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  background-color: #3aafa9;
  color: #fff;
  padding-bottom: 3rem;
  border-bottom: 3px solid black;
}

.navbar__title {
  font-size: 4.8rem;
  font-weight: 700;
}

.navbar__items {
  padding-right: 5rem;
  display: flex;
  list-style: none;
  grid-gap: 3rem;
  gap: 3rem;
  font-size: 3rem;
  text-decoration: none;
}

.item:not(:last-child) {
  padding-right: 3rem;
  border-right: black solid 0.3rem;
}

.item a:link,
.item a:visited {
  color: #000;
  font-weight: 500;
  font-size: 2.8rem;
  text-decoration: none;
}

.home__container {
  padding: 5rem;
  display: flex;
  flex-direction: column;
  grid-gap: 5rem;
  gap: 5rem;
  min-height: 100vh;
  background-color: #3aafa9;
  color: #000;
  font-family: 'Inter', sans-serif;
}

.title {
  font-size: 5rem;
}

.description {
  font-size: 3rem;
  line-height: 1.5;
}

.calculator__page {
  padding: 5rem;
  background-color: #3aafa9;
  min-height: 100vh;
}

.title {
  font-family: 'Inter', sans-serif;
  font-size: 5rem;
  font-weight: 500;
}

.container {
  top: 30%;
  left: 60%;
  position: absolute;
  background-color: rgb(134, 132, 132);
  border-top: 3px solid #2bb9dd;
  box-shadow: 0.1px 0.1px 2rem 0.1rem rgba(0, 0, 0, 0.267);
  border-radius: 9px;
  overflow: hidden;
}

.result {
  text-align: end;
  font-size: 2rem;
  color: #fff;
  background-color: rgb(36, 36, 36);
  box-shadow: 0.1px 0.2rem 1rem 0.1rem inset black;
  display: flex;
  align-items: center;
  justify-content: end;
  padding: 1rem;
  height: 6rem;
}

.show-result {
  font-family: 'Inter', sans-serif;
  font-weight: 500;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(8rem, auto);
  margin: 0.01rem;
}

.zero {
  grid-column-end: span 2;
}

.options {
  background-color: #2bb9dd;
}

button {
  padding: 2rem 4rem;
  font-size: 1.5rem;
  border: none;
  cursor: pointer;
}

.quote__page {
  position: absolute;
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 80vh;
  padding: 15rem;
  grid-gap: 5rem;
  gap: 5rem;
  background-color: #3aafa9;
  min-height: 100vh;
  font-family: 'Inter', sans-serif;
}

.quote__page__title {
  font-size: 4rem;
}

.author {
  font-size: 4rem;
  margin-left: auto;
}

